import React, { PureComponent } from 'react'
import {
  message,
  Button,
  Tabs,
  Modal
} from 'antd'
import style from './style/Tool.scss'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
const { TabPane } = Tabs
import { setVariant } from 'Variant'
const confirm = Modal.confirm
@connect(
  state => ({ ...state.businessExceedsLOPrint }),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  // 
  handleOut = () => {
    const { selectedRows } = this.props
    if (!selectedRows.length) {
      message.error('')
      return
    }
    let ids = selectedRows.map((o) => o.externalOrderCode)
    
    setVariant('wms/businessExceedsLOPrint', ids)
    window.open('/wms/businessExceedsLOPrint/components/Print', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
  }
  // 
  query = (tableActiveKey) => {
    const { handleGetList, searchParams, size, update } = this.props
    let reqObj = { ...searchParams }
    update({ tableActiveKey })
    handleGetList({ searchParams: { ...reqObj, page: 1, printFlag: tableActiveKey, size } }, true)
  }
  render() {
    const { tableActiveKey, tabN, tabY, modalTuttonLoading } = this.props
    return (
      <div className={style['table-tool']} style={{ paddingBottom: 0 }}>
        <Button type="primary" className={style.btn} onClick={this.handleOut} loading={modalTuttonLoading} ></Button>
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`(${tabN || 0})`} key="N"></TabPane>
          <TabPane tab={`(${tabY || 0})`} key="Y"></TabPane>
        </Tabs>
      </div>
    )
  }
}



