import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.businessExceedsLOPrint}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '',
      width: 100,
      dataIndex: 'productCode',
      key: 'productCode'
    }, {
      title: '',
      width: 100,
      dataIndex: 'productName',
      key: 'productName'
    }, {
      title: '',
      width: 100,
      dataIndex: 'unit',
      key: 'unit'
    }, {
      title: '',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '',
      width: 100,
      dataIndex: 'planQty',
      key: 'planQty'
    }, {
      title: '',
      width: 100,
      dataIndex: 'shippedQty',
      key: 'shippedQty'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // tabskey
  _getAsyncData() {
    const {handleRecordDetil, recordData} = this.props
    handleRecordDetil({searchParams: {externalOrderCode: recordData.externalOrderCode}})
  }
  // 
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowRecordModal: false})
  }
  render() {
    const {deliveryTableLoading, modalData, printLoading} = this.props
    return (
      <Modal
        title=""
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={940}
      >
        { 
          printLoading ? <div className="printLoadingSty">
            <Spin spinning={printLoading} size="large" />
          </div> :
          modalData.map((o, i)=>{
            return(
              <div style={{marginTop: 30}} key={i}>
                <Row>
                  <Col span={8}>{`${o.orderCode}`}</Col>
                  <Col span={8}>{`${o.externalOrderCode}`}</Col>
                  <Col span={8}>{`${o.status?o.status:''}`}</Col>
                  <Col span={8}>{`${o.sendTime?o.sendTime:''}`}</Col>
                  <Col span={8}>{`${o.updateBy?o.updateBy:''}`}</Col>
                </Row>
                <div className='table-group' style={{margin: 0}}>
                  <Table
                    rowKey={(record, index) => index}
                    loading={deliveryTableLoading}
                    columns={this.columns}
                    scroll={{x: 500, y: 400}}
                    dataSource={o.shippedOrderItemList}
                    pagination={false}
                  />
                </div>
              </div>
            )
          })
        }
        </Modal>
      )
  }
}



