import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  message,
  Input
} from 'antd'
import style from './style/Form.scss'
import {connect} from 'react-redux'
import {CsxSelect, CsxMultipleSearch} from 'Form'
import {API_SHIPPEDORDERS_BUSINESSSHIPPEDKEYVALUES, API_SHOPINFOBYSHOPCODE_KEYVALUES, API_MERCHANTPRIORITYCHOICE_KEYVALUES} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import moment from 'moment'
const {RangePicker} = DatePicker

@connect(
  state => ({...state.businessExceedsLOPrint, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  //    - 
  initDateOpt(values) {
    const {update} = this.props
    const preWeek = moment().subtract(7, 'd')
    update({searchParams: {initDate: [preWeek, moment()]}})
  }
  // 
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    const {initDate = []} = searchParams
    // 
    if (!initDate || (Array.isArray(initDate) && initDate.length < 2)) {
      message.error('')
    }
    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
    // 
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        initDate,
        orderCode,
        businessType,
        shopCode,
        shopType,
        planDate
      }
    } = this.props
    return (
      <Form className={style.form}>
        <Row
          type="flex"
          justify="start"
        >
          <Col span={6}>
            <div className='commonInput'>
              <label></label>
              <div className='box'>
                <Input placeholder='' value={orderCode} onChange={(e) => {
                  this.onInputChange({orderCode: e.target.value})
                }} />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_SHIPPEDORDERS_BUSINESSSHIPPEDKEYVALUES}
              label=''
              searchParams={{orderType: 'T'}}
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={12}>
            <div className='commonInput'>
              <label></label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({initDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxMultipleSearch
              dateApi={API_SHOPINFOBYSHOPCODE_KEYVALUES}
              label=''
              searchParamsKey='shopCode'
              selectItem={{data: shopCode, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_MERCHANTPRIORITYCHOICE_KEYVALUES}
              label=''
              selectItem={{data: shopType, key: 'merchantType', valueKey: 'merchantType'}}
              onChangeSelect={(value) => {
                this.onInputChange({shopType: value})
              }}
            />
          </Col>
          <Col span={12}>
            <div className='commonInput'>
              <label>:</label>
              <div className='box' style={{marginRight: 0}}>
                <RangePicker onChange={(moment, string) => {
                  this.onInputChange({planDate: moment})
                }}
                  format={'YYYY-MM-DD'}
                  value={planDate}
                />
              </div>
            </div>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end" >
              <Button type="primary" className={style.mr10} onClick={this.query} ></Button>
              <Button type="primary" className={style.mr10} onClick={this.reset} ></Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



