import React, { PureComponent } from 'react'
import {
  Table,
  Icon,
  Popover
} from 'antd'
import { connect } from 'react-redux'
import { mapDispatchToProps } from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'

@connect(
  state => ({ ...state.businessExceedsLOPrint, userLoation: state.userLoation }),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '',
      width: 150,
      dataIndex: 'externalOrderCode',
      key: 'externalOrderCode',
      render: (text, record, index) => {
        return (
          <div className='tableOperation'>
            <span className='blue' onClick={()=>{this.onEdit(record)}}>{text}</span>
          </div>
        )
      }
    }, {
      title: '',
      width: 150,
      dataIndex: 'businessTypeDesc',
      key: 'businessTypeDesc',
    }, {
      title: '',
      width: 200,
      dataIndex: 'planDate',
      key: 'planDate'
    }, {
      title: '',
      width: 200,
      dataIndex: 'createTime',
      key: 'createTime'
    }, {
      title: '',
      width: 150,
      dataIndex: 'createBy',
      key: 'createBy'
    }, {
      title: '',
      width: 100,
      dataIndex: 'printTimes',
      key: 'printTimes'
    }, {
      title: '',
      width: 100,
      dataIndex: 'orderCount',
      key: 'orderCount'
    }]
  }
  componentDidMount() {
    const { handleGetList, searchParams, page, size, userLoation } = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({ searchParams: { page, size, ...searchParams } })
  }
  // 
  componentWillReceiveProps(newProps, newStates) {
    const { handleGetList, userLoation } = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({ searchParams: { page: 1, size: newProps.size, ...newProps.searchParams } })
    }
  }
  // 
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  //
  onEdit = (record) => {
    const { update } = this.props
    console.log(record)
    update({ isShowRecordModal: true, modalTitle: '', recordData: record })
  }

  //  columns
  // isAsc  true, false
  onChange = (pagination, filters, sorter, extra) => {
    const { handleGetList, searchParams, page, size, tableActiveKey } = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({ searchParams: { page, size, printFlag: tableActiveKey, ...searchParams, isAsc: true, columns: sorter.field } }, true)
      } else {
        handleGetList({ searchParams: { page, size, printFlag: tableActiveKey, ...searchParams, isAsc: false, columns: sorter.field } }, true)
      }
    } else {
      // 
      handleGetList({ searchParams: { page: pagination.current, size: pagination.pageSize, printFlag: tableActiveKey, ...searchParams } }, true)
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => ` ${total} `, // 
      showQuickJumper: true, // 
      current: page,
      pageSize: size,
      showSizeChanger: true, //  pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const { update } = this.props
        update({ selectedRowKeys, selectedRows })
      },
      selectedRowKeys
      // getCheckboxProps: record => ({
      //   disabled: record.status !== 6 && record.status !== 7 && record.status !== 8 // Column configuration not to be checked
      // })
    }
    return (
      <div className='table-group'>
        <Table
          rowKey={(record, index) => index}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{ x: 'max-content', y: 400 }}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



